import React from 'react'
import mono from "../assets/mono_picture_api.jpg"
export default function Footer() {
  return (
    <div className="footer">
      <div className='indent'>
        <span className='footer_text'>
        {window.outerWidth > 765 ? 'Сайт створено frontend розробником Дяченко Людмилою' : 'Сайт створено Дяченко Людмилою'}
        </span>
      </div>
      <div className='parag'>
        <span className='footer_text'>При підтримці: </span> 
        <img height="15" alt='Logotype National Bank of Ukraine' src="data:image/png;base64," />
        <span className='footer_text'>
        {window.outerWidth > 765 ? 'Національного банку України' : 'НБУ'}
        </span>
        <a href="https://bank.gov.ua/ua/open-data/api-dev" target='_blank' className='footer_link'> API </a> 
        <span className='footer_text'>та</span><img src={mono} height="14" alt='Logotype monobank'/>
        <span className='footer_text'>Monobank</span><a href='https://api.monobank.ua/docs/index.html' target='_blank' className='footer_link'>API</a> 
      </div>
    </div>
  )
}
